<template>
	<view class="content">
		<view class="cu-form-group">
			<text class="tit">测量类型</text>
			<picker @change="bindPickerType" :value="details.measuring_type_id" :range="pickerType">
				<view class="picker">
					{{pickerType[details.measuring_type_id]}}
				</view>
			</picker>
		</view>
		<view class="cu-form-group">
			<view class="title">位置</view>
			<input maxlength="30" class="text-right" placeholder="如厨房" v-model="details.location" placeholder-class="placeholder"></input>
		</view>
		<view class="cu-form-group">
			<view class="title">长度</view>
			<input maxlength="11" class="text-right" type="digit" placeholder="单位米" v-model="details.length" placeholder-class="placeholder"></input>
		</view>
		<view class="cu-form-group">
			<view class="title">宽度</view>
			<input maxlength="11" class="text-right" type="digit" placeholder="单位米" v-model="details.width" placeholder-class="placeholder"></input>
		</view>
		<view class="cu-form-group align-start">
			<view class="title">备注</view>
			<textarea maxlength="200" v-model="details.remark" placeholder="备注信息"></textarea>
		</view>
		<view class="cu-bar bg-white margin-top">
			<view class="action">
				图片
			</view>
			<view class="action">
				{{details.img_list.length}}/4
			</view>
		</view>
		<view class="cu-form-group">
			<view class="grid col-4 grid-square flex-sub">
				<view class="bg-img" v-for="(item,index) in details.img_list" :key="index" @tap="ViewImage" :data-url="item.img">
				 <image :src="item.img" mode="aspectFill"></image>
					<view class="cu-tag bg-red" @tap.stop="DelImg" :data-index="index">
						<text class='cuIcon-close'></text>
					</view>
				</view>
				<view class="solids" @tap="ChooseImage" v-if="details.img_list.length<4">
					<text class='cuIcon-cameraadd'></text>
				</view>
			</view>
		</view>
		<view class="cu-list menu sm-border margin-top">
			<view class="cu-item arrow" @click="goodsAllocated()">
				<view class="content">
					<text>分配商品</text>
				</view>
			</view>
		</view>
		<view class="goods-section margin-top-sm padding-top-sm" v-for="(item, index) in details.good_indent_commodity" :key="index" @click="selectGoods(item)">
			<view class="g-item">
				<image :src="item.img"></image>
				<view class="right">
					<text class="title clamp">{{item.name}}</text>
					<text v-if="item.good_sku_id" class="spec">{{item.specification}}</text>
					<view class="price-box">
						<text class="price text-red text-price">{{item.price}}</text>
						<button class="cu-btn round text-right delete-button" @click.stop="deleteCartItem(index)">删除</button>
					</view>
				</view>
			</view>
		</view>
		<button class="add-btn" @click="confirm">提交</button>
	</view>
</template>

<script>
	import Measurement from '../../api/measurement'
	import MeasuringType from '../../api/measuringType'
	import { objectURLToBlob } from '../../utils'
	export default {
		data() {
			return {
				pickerType: ['请选择'],
				id: '',
				oid: '',
				details: {
					good_indent_id: 0,
					measuring_type_id: 0,
					location: '',
					length: '',
					width: '',
					img_list: [],
					remark: '',
					good_indent_commodity: []
				}
			}
		},
		onLoad(option){
			this.oid = option.oid
			this.details.good_indent_id = this.oid
			this.id = option.id
			this.index = option.index
			this.getList()
		},
		methods: {
			async getList(){
				let title = '新增测量配置'
				await MeasuringType.getList({
					limit: 100,
					page: 1
				},function(res){
					if(res.data.length>0){
						res.data.forEach(item=>{
							that.pickerType.push(item.name)
						})
						
					}
					
				})
				const that = this
				if(this.id > 0){
					title = '编辑测量配置'
					Measurement.getDetails(this.id,{},function(res){
						if(res.good_indent_commodity.length > 0){
							for(var k in res.good_indent_commodity){
								if(res.good_indent_commodity[k].good_sku){
									res.good_indent_commodity[k].good_sku.product_sku.forEach(item=>{
										if(res.good_indent_commodity[k].specification){
											res.good_indent_commodity[k].specification+= item.value + ';'
										}else{
											res.good_indent_commodity[k].specification = item.value + ';'
										}
										
									})
									res.good_indent_commodity[k].specification = res.good_indent_commodity[k].specification.substr(0,res.good_indent_commodity[k].specification.length-1)
								}
							}
						}
						that.details = res
					})
				}
				uni.setNavigationBarTitle({
					title
				})
			},
			
			//提交
			confirm(){
				let data = this.details
				const that = this
				if(!data.measuring_type_id){
					this.$api.msg('请选择测量类型');
					return;
				}
				if(!data.location){
					this.$api.msg('请填写位置');
					return;
				}
				if(!data.length){
					this.$api.msg('请填写长度');
					return;
				}
				if(!data.width){
					this.$api.msg('请填写宽度');
					return;
				}
				data.area = (data.length * data.width).toFixed(3)
				data.type_show = this.pickerType[data.type]
				if(data.id){
					Measurement.updateSubmit(data,function(res){
						that.$api.prePage().refreshMeasurementList(that.index, data)
						that.$api.msg(`测量配置修改成功`);
						setTimeout(()=>{
							uni.navigateBack()
						}, 800)
					})
				}else{
					Measurement.createSubmit(data,function(res){
						data.id = res
						that.$api.prePage().refreshMeasurementList(that.index, data)
						that.$api.msg(`测量配置添加成功`);
						setTimeout(()=>{
							uni.navigateBack()
						}, 800)
					})
				}
			},
			// 测量类型
			bindPickerType(e) {
				this.details.measuring_type_id = e.detail.value
			},
			// 分配商品回调
			refreshGoodsAllocated(data){
				this.details.good_indent_commodity = []
				if (data.length > 0) {
					data.forEach(item=>{
						if(item.checked){
							this.details.good_indent_commodity.push(item)
						}
					})
				}
			},
			//删除分配的商品
			deleteCartItem(index){
				this.details.good_indent_commodity.splice(index, 1)
			},
			ChooseImage() {
				let that = this
				let uploadFile = ''
				uni.chooseImage({
					count: 4, //默认9
					sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
					success: (res) => {
						for(var n = 0; n < res.tempFilePaths.length; n++) {
							uni.uploadFile({
							  url: uni.getStorageSync('applyGoodSecret').host + 'uploadPictures',
							  filePath: res.tempFilePaths[n],
							  name: 'file',
							  header: {
								'apply-secret': uni.getStorageSync('applyGoodSecret').secret,
								'Authorization': 'Bearer ' + uni.getStorageSync('applytoken')
							  },
							  formData: {
								type: 1,
								size: 1024 * 500
							  },
							  success(res) {
								that.details.img_list.push({
									img: res.data
								})
							  },
							  fail(res) {
								that.$api.msg(res.message);
								return false
							  }
							})
						}
					}
				});
			},
			ViewImage(e) {
				uni.previewImage({
					urls: this.details.img_list,
					current: e.currentTarget.dataset.url
				});
			},
			DelImg(e) {
				uni.showModal({
					title: '提示',
					content: '确定要删除该图片吗？',
					cancelText: '再看看',
					confirmText: '确实',
					success: res => {
						if (res.confirm) {
							this.details.img_list.splice(e.currentTarget.dataset.index, 1)
						}
					}
				})
			},
			goodsAllocated(){
				let good = []
				this.details.good_indent_commodity.forEach(item=>{
					good.push(item.id)
				})
				uni.navigateTo({
					url: `/pages/order/goods_allocated?oid=${this.oid}&good=${good}`
				})
			}
		}
	}
</script>

<style lang="scss">
	page{
		background: $page-color-base;
		padding-top: 16upx;
	}

	.row{
		display: flex;
		align-items: center;
		position: relative;
		padding:0 30upx;
		height: 110upx;
		background: #fff;
		
		.tit{
			flex-shrink: 0;
			width: 120upx;
			font-size: 30upx;
			color: $font-color-dark;
		}
		.input{
			flex: 1;
			font-size: 30upx;
			color: $font-color-dark;
		}
		.icon-shouhuodizhi{
			font-size: 36upx;
			color: $font-color-light;
		}
	}
	.default-row{
		margin-top: 16upx;
		.tit{
			flex: 1;
		}
		switch{
			transform: translateX(16upx) scale(.9);
		}
	}
	.add-btn{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 690upx;
		height: 80upx;
		margin: 60upx auto;
		font-size: $font-lg;
		color: #fff;
		background-color: $base-color;
		border-radius: 10upx;
		box-shadow: 1px 2px 5px rgba(219, 63, 96, 0.4);
	}
	.goods-section {
		background: #fff;
		padding-bottom: 1px;
	
		.g-header {
			display: flex;
			align-items: center;
			height: 84upx;
			padding: 0 30upx;
			position: relative;
		}
	
		.logo {
			display: block;
			width: 50upx;
			height: 50upx;
			border-radius: 100px;
		}
	
		.name {
			font-size: 30upx;
			color: $font-color-base;
			margin-left: 24upx;
		}
	
		.g-item {
			display: flex;
			margin: 20upx 30upx;
	
			image {
				flex-shrink: 0;
				display: block;
				width: 140upx;
				height: 140upx;
				border-radius: 4upx;
			}
	
			.right {
				flex: 1;
				padding-left: 24upx;
				overflow: hidden;
			}
	
			.title {
				font-size: 30upx;
				color: $font-color-dark;
			}
	
			.spec {
				font-size: 26upx;
				color: $font-color-light;
			}
	
			.price-box {
				display: flex;
				align-items: center;
				font-size: 32upx;
				color: $font-color-dark;
				padding-top: 10upx;
	
				.price {
					margin-bottom: 4upx;
				}
				.number{
					font-size: 26upx;
					color: $font-color-base;
					margin-left: 20upx;
				}
				.delete-button{
					position:absolute;
					right:20upx;
				}
			}
			.text-icon{
				font-size: 40upx;
			}
			.step-box {
				position: relative;
			}
		}
	}
</style>
